import { Injectable } from '@angular/core';

import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  private URL = document.getElementById("getUrl")['value'];

  constructor(private http: HttpClient) { }

  getDataGeneral(data){
    // Initialize Params Object
    let params = new HttpParams();

    // Begin assigning parameters
    params = params.append('mes', data.mes);
    params = params.append('ano', data.ano);
    params = params.append('acargo', data.acargo);
    
   
    
    return this.http.get(this.URL + 'reporte_general/reporte_grafico', {params: params});
  }

  getDataBeheavior(data){
    // Initialize Params Object
    let params = new HttpParams();

    // Begin assigning parameters
    params = params.append('id', data.id);
    params = params.append('mes', data.mes);
    params = params.append('ano', data.ano);
    params = params.append('acargo', data.acargo);

   
    return this.http.get(this.URL + 'reporte_general/reporte_grafico_comportamiento', {params: params});
  }

  //Search By Cycle
  getDataGeneralCycle(data){
    // Initialize Params Object
    let params = new HttpParams();

    // Begin assigning parameters
    params = params.append('cycle', data.cycle);
    params = params.append('ano', data.ano);
    params = params.append('acargo', data.acargo);
    
   
    

    return this.http.get(this.URL + 'reporte_general/reporte_grafico_cycle', {params: params});
  }

  getDataBeheaviorCycle(data){
    // Initialize Params Object
    let params = new HttpParams();

    // Begin assigning parameters
    params = params.append('id', data.id);
    params = params.append('cycle', data.cycle);
    params = params.append('ano', data.ano);
    params = params.append('acargo', data.acargo);
    return this.http.get(this.URL + 'reporte_general/reporte_grafico_comportamiento_cycle', {params: params});
  }

}
