import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ValorationsReportComponent } from './Components/valorations-report/valorations-report.component'
import { ComparativeReportComponent } from './Components/comparative-report/comparative-report.component'

const routes: Routes = [
  { path: '', component: ValorationsReportComponent },
  { path: 'comparative', component: ComparativeReportComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
