  //generate color
  getRandomColor() {
    var length = 6;
    var chars = '0123456789ABCDEF';
    var hex = '#';
    while(length--) hex += chars[(Math.random() * 16) | 0];
    return hex;
  }

  download(){
    //Convert Grafiph to image
    var canvas = $("canvas").get(0);
    
    var dataURL = canvas.toDataURL();
    let doc = new jsPDF()
  
    let specialElementHandlers = {
      '#editor': function(element, renderer) {
        return true;
      }
    }
    
    doc.text('Reporte Gráfico Actualización de datos NewStetic',  40, 19)
    doc.addImage(dataURL,  0, 30, 200, 100);
  
    doc.save('ReporteGraficoActualizaciondeDatosNewStetic.pdf')
  }

  searchData(){
    this.data.inCharge == 'true' ? this.title_report = 'Con Personal a cargo' : this.title_report = 'Sin Personal a cargo'
    let firstSendData = {
      ano: this.data.first_age,
      cycle: this.data.first_cycle,
      acargo: this.data.inCharge
    }

    this.dataService.getDataGeneralCycle(firstSendData).subscribe(data =>{
      this.list_competencies = data['competencies_label']
      let label_competencies = []
      data['competencies_label'].map(competency =>{
        label_competencies.push(competency.name)
      })

      this.barChartDataGeneral = [{data: [data['indicador_data']], label: 'Indicador'}]
      this.barChartLabelsCompetencies = label_competencies
      this.barChartDataCompetencies = [{data: data['competencies_data'], label: 'Competencias'}]
    })

    let lastSendData = {
      ano: this.data.last_age,
      cycle: this.data.last_cycle,
      acargo: this.data.inCharge
    }

    this.dataService.getDataGeneralCycle(lastSendData).subscribe(data =>{

      this.barChartDataCompetencies.push({data: data['competencies_data'], label: 'Competencias'})
    })

  }

