import { Component, OnInit, Input } from '@angular/core';

import * as $ from 'jquery';
import * as jsPDF from 'jspdf';
import { ChartOptions, ChartType, ChartDataSets } from 'chart.js';
import { Label } from 'ng2-charts';
import * as html2canvas from 'html2canvas'
import * as pluginDataLabels from 'chartjs-plugin-datalabels';

/*Services */
import { DataService } from '../../Services/data.service'

@Component({
  selector: 'app-graphic-line',
  templateUrl: './graphic-line.component.html',
  styleUrls: ['./graphic-line.component.scss']
})
export class GraphicLineComponent implements OnInit {
  
  @Input() index: number;
  @Input() sendData;
  @Input() competency;

  public barChartOptions: ChartOptions = {
    responsive: true,
    // We use these empty structures as placeholders for dynamic theming.
    scales: { xAxes: [{}], yAxes: [{
      ticks: {
        beginAtZero: true
      }
    }] },
    plugins: {
      datalabels: {
        anchor: 'end',
        align: 'end',
      }
    }
  };
  public barChartLabels: Label[] = ['2006', '2007', '2008', '2009', '2010', '2011', '2012'];
  public barChartType: ChartType = 'bar';
  public barChartLegend = true;
  public barChartPlugins = [pluginDataLabels];

  public barChartData: ChartDataSets[] = [
    { data: [65, 59, 80, 81, 56, 55, 40], label: 'Series A' },
    { data: [28, 48, 40, 19, 86, 27, 90], label: 'Series B' }
  ];

  constructor(
    public dataServices: DataService
  ) { }

  ngOnInit() {
    let sendData = {
      id: this.competency['id'],
      ano: this.sendData.age,
      cycle: this.sendData.cycle,
      acargo: this.sendData.inCharge
    }
    
    this.dataServices.getDataBeheaviorCycle(sendData).subscribe(data =>{
      this.barChartLabels = data['behavior_label']
      this.barChartData = [{data: data['behavior_data'], label: 'Comportamientos'}]
    })
  }

  // events
  public chartClicked({ event, active }: { event: MouseEvent, active: {}[] }): void {
    console.log(event, active);
  }

  public chartHovered({ event, active }: { event: MouseEvent, active: {}[] }): void {
    console.log(event, active);
  }

  public randomize(): void {
    // Only Change 3 values
    const data = [
      Math.round(Math.random() * 100),
      59,
      80,
      (Math.random() * 100),
      56,
      (Math.random() * 100),
      40];
    this.barChartData[0].data = data;
  }

}
